import React, { useState } from "react";

const TheGame = () => (
  <div>
    <h1>O Jogo</h1>

    <p>Neste jogo aprenderemos um pouco de Histria e Geografia</p>

    <p>Tambm  falado um pouco sobre a cultura de cada lugar</p>

    <p>O objetivo  descobrir qual pas de cada nvel.</p>
    <p>Para isso voc entrar no jogo como se fosse uma pessoa na rua nesse pas.</p>

    <p>
      Voc estar andando por algum local importante do pas,
      que pode ser um marco histrico ou apenas um local turstico.
    </p>

    <p>
      O primeiro nvel  apenas um tutorial, e no h limite de tempo ou de palpites.
      Mas ainda tem limite de dicas.
    </p>
  </div>
);


const Time = ({ limit }) => (
  <div>
    <h1>O Cronmetro</h1>

    <p>Para ser um jogo mais interessante, adicionamos um tempo para que voc tenha uma dificuldade extra.</p>

    <p>Voc ter {limit} segundos para se movimentar pelo jogo.</p>

    <p>O tempo pausar enquanto voc estiver vendo as dicas ou vendo o mapa para dar um palpite.</p>
  </div>
);

const Navigation = () => (
  <div>
    <h1>Como se mover</h1>

    <p>Dentro do jogo, voc estar vendo a vista da rua do lugar que voc tem que acertar.</p>

    <p>
      Voc pode usar o mouse como forma de navegao, clicando e movendo o mouse para movimentar a cmera.
      E clicar nas setas que aparecerem pelo cho para se movimentar.
    </p>

    <p>
      Voc pode usar as setas     do teclado para se virar e para andar pelo jogo,
      isso  til para quando voc est atrs de informaes sobre o local,
      e a posio atual no est ajudando.
    </p>

    <p>
      Voc pode usar os botes de  e  para se aproximar ou afastar,
      isso  til para conseguir ler melhor alguma informao da tela.
    </p>
  </div>
);

const Tips = ({ limit }) => (
  <div>
    <h1>Dicas</h1>

    <p>No boto de <div className='fake-button'>Dicas</div>, voc ter 10 fatos histricos, geogrficos e/ou curiosidades sobre o pas.</p>

    <p>Enquanto estiver vendo as dicas, o cronmetro parar, mas voc ter 30 segundos para escolher e ler.</p>

    <p>Ao final desses 30 segundos a janela fechar e voc ter que abrir de novo caso queira ler novamente.</p>

    <p>Voc tem um limite de {limit} dicas disponveis para ler, ento escolha com sabedoria quais voc vai revelar.</p>

    <p>No precisa se preocupar, por ao fim do nvel voc poder ver todas com calma, para aprender mais sobre o local.</p>
  </div>
);

const Guess = ({ limit }) => (
  <div>
    <h1>Palpite</h1>

    <p>No boto de <div className='fake-button'>J sei!</div>, voc ter a possibilidade de dar um palpite.</p>

    <p>Ao todo voc ter {limit} palpites disponiveis.</p>

    <p>
      Um mapa abrir e voc poder escolher um pas para sua tentativa, e voc ter 30 ssegundos para escolher,
      e caso esse tempo esgote, voc poder abrir a janela novamente para escolher, sem perder palpites.
    </p>

    <p>
      Caso o pas esteja correto, ento voc vai receber uma mensagem de sucesso
      com as informaes sobre o palpite, quanto tempo levou e quantas dicas foram necessrias.
    </p>

    <p>
      Caso o pas selecionado no for o correto, ele aparecer em vermelho no mapa,
      e voc poder tentar novamente, se ainda tiver palpites restantes.
    </p>

    <p>
      Ao final dos palpites, caso no tenha acertado,
      voc ainda poder ver qual pas  o local e as curiosidades.
    </p>
  </div>
);

export function Tutorial({ name: initialName = '', timeLimit, tipsLimit, guessLimit, onClose, onName }) {
  const [step, setStep] = useState(initialName ? 'game' : 'name');
  const [name, setName] = useState(initialName);

  const steps = {
    game: { label: 'O jogo', comp: <TheGame /> },
    navigation: { label: 'Navegao', comp: <Navigation /> },
    time: { label: 'Tempo', comp: <Time limit={timeLimit} /> },
    tips: { label: 'Dicas ', comp: <Tips limit={tipsLimit} /> },
    guess: { label: 'Palpite', comp: <Guess limit={guessLimit} /> },
  };

  return (
    <div className="tutorial">
      {step === 'name' ? (
        <div className="request-name">
          <h1>
            Ol! Qual o nome do grupo?
          </h1>

          <input type="text" value={name} onChange={event => setName(event.target.value)} />

          <button onClick={() => {
            if (!name) {
              return;
            }
            onName(name);
            setStep('game');
          }}>Confirmo!</button>
        </div>
      ) : (
        <>
            <button className="close" onClick={onClose}>
              Sair
            </button>

          {steps[step]?.comp}
          <div className="controls">
            {Object.entries(steps).map(([key, data]) => (
              <button
                key={key}
                className={step === key ? 'active' : ''}
                onClick={() => setStep(key)}
              >
                {data.label}
              </button>
            ))}
          </div>
        </>
      )}
    </div>
  );
}
